      <div class="con-right" v-show="type===1">
        <div class="explain" v-show="item==='MA'">
          <p>移动平均线[Moving Average]移动平均线(Moving Average)</p>
           <p>移动平均线是以道.琼斯的“平均成本概念’为理论基础，采用统计学中“移动平均"的原理，将一段时期内的价格平均值连成曲线，用来显示价格的历史波动情况。rMA默认设置的周期为5、10、20、40。</p>
          </div>
        <div class="explain" v-show="item==='BOLL'">
          <p>布林线指标(Bollinger Bands)</p>
          <p>BOLL布林轨道由JOHN BOLLINGER所创，计算方法是先计算收盘价的“标准差”，再求取收盘价的“信赖区间”，布林线是一-个路径型指标，由上限和下限两条线构成一个带状的路径。标的价格超越上限时代表超买，标的价格超越下限时代表超卖。标的价格游走在“上限"和“下限”的带状间内，这条带状区的宽窄，随着价格波动幅度的大小而变化，涨跌幅度加大时，带状区会变宽，涨跌幅度狭小盘整时，带状区会变窄。也就是说，布林线是属于变异性的，可以随着标的价格的变化而自动调整位置。r在函数的使用上周期n为计数周期，参数q为特性参数。
            BOLL默认设置周期为20，默认设置参数为2。</p>
        </div>
        <div class="explain" v-show="item==='EMA'">
          <p>指数平均数指标(Exponential Moving Average)</p>
          <p>指数平均数指标是一种趋向类指标，其构造原理仍然是对价格收盘价进行算术平均，并根据计算结果来进行分析，用于判断价格未来的变动趋势。
            EMA指标由于其计算公式中着重考虑了价格当期行情的权重,因此它在使用中克服了MACD指标信号对于价格走势的滞后性。同时也在一定程度中消除了DMA指标在某些时候对于价格走势所产生的信号提前性，是一个非常有效的分析指标。
            EMA默认设置的周期为5、10、 20。</p>
        </div>
        <div class="explain" v-show="item==='MACD'">
          <p>指数平滑异同移动平均线(Moving Average Convergence / Divergence)</p>
          <p>MACD指标是一个很流行又管用的指标，可以去除掉移动平均线频烦的假信号缺陷又能确保移动平均线最大的战果。MACD是利用两条不同速度(长期与中期)的平滑移动平均线(EMA) 来计算二者之间的差离状况作为研判行情的基础。在绘制的图形上，DIF与DEA形成了两条快慢移动平均线，买进卖出信号也就决定于这两条线的交叉点。很明显，MACD是一个中长期趋势的投资技术工具。用法如下:<br>
            1、DIFF、 DEA均为正，DIFF向上突破DEA，买入信号;<br>2、DIFF、 DEA均为负，DIFF向下跌破DEA，卖出信号;<br>3、DEA线与K线发生背离，行情反转信号;<br>
            4、分析MACD柱状线，由红变绿(正变负]，卖出信号;由绿变红，买入信号；<br>5、MACD在0界限以上为多头市场，反之为空头市场;<br>6、日线、周线、月线、分钟线配合运用效果会更好; <br>MACD默认设置短周期为12，长周期为26，移动平均周期为9</p>
        </div>
        <div class="explain" v-show="item==='VOL'">
          <p>成交量指标(Volume)</p>
          <p>成交量是指在某一时段内达成交易的数量。它可以在分时图中绘制，也可以在K线图中绘制。成交量的变化反映了资金进出市场的情况，成交量是判断市场走势的重要指标。</p>
        </div>
        <div class="explain" v-show="item==='BBI'">
          <p>多空均线</p>
          <p>1.股价位于BBI上方,视为多头市场;</p>
          <p>2.股价位于BBI下方，视为空头市场。</p>
        </div>
        <div class="explain" v-show="item==='DMA'">
          <p>平行线差指标(Different of Moving Average)</p>
          <p>收盘价的短期平均与长期平均的差除以短期天数，可以得到DMA; DMA的N日平均为AMA。<br>
            1、DDD快线上穿AMA慢线，为买入信号提示，表示趋势将向.上运行;<br>2、DDD快线下穿AMA慢线，为卖出信号提示，表示趋势将向下运行。DMA默认设置的短周期为10,长周期为50。</p>
        </div>
        <div class="explain" v-show="item==='KDJ'">
          <p>随机指标(KDJ Index)</p>
          <p>随机指标在图表上采用K%和D%两条线，在设计中综合了动量观念、强弱指标与移动平均线的优点，在计算过程中主要研究高低价位与收市价的关系,反映价格走势的强弱和超买超卖现象。它的主要理论依据是:当价格上涨时，收市价倾向于接近当日价格区间的上端;相反，在下降趋势中收市价趋向于接近当日价格区间的下端。在股市和期市中，因为市场趋势上升而未转向前，每日多数都会偏向于高价位收市，而下跌时收市价就常会偏于低位。随机指数在设计中充分考虑价格波动的随机振幅与中短期波动的测算，使其短期测市功能比移动平均线更加准确有效，在市场短期超买超卖的预测方面又比强弱指数敏感，因此，这一指标被投资者广泛采用。应用法则如下:<br>1、D值在70以上时，市场呈现超买现象。D值在30以下时市场则呈现超卖现象;<br>
            2、当K线发生倾斜度趋于平缓时，是为警告信号;<br>
            3、当K值大于D值，显示目前是向上涨升的趋势，因此在图形上K%向.上突破D线时，即为买进的信号;<br>
            4、当D值大于K值，显示目前的趋势是向下跌落，因此在图形上K%向下跌破D线，即为卖出信号;<br>
            5、K%线与D%线的交叉，须在80以下，20以下信号才较为正确。K%与D%，线在特性.上与强弱指标一-样，当K值与D值在70以上，已显示超买的现象，30以下出现超卖的现象。唯强弱指标不能明显的显示买卖时机，而KD线则可以达到此目的;<br>6、背离信号产生时，亦为非常正确的买进、卖出时机;7、交易量太小的标的不适用KD指标;<br>
            8、KD值在50左右交叉且为盘局时，此指标不能当成明显的买卖信号;<br>
            9、J >100时为超买，K -10时为超卖，反应较K及D为快。KD指标是计算n日内买卖超情况，再加上平均线观念而求出的可设定计算期(即n值)，-般M1，M2都设成3,若N设成0，表示不画J线。KDJ默认设置的周期为9、3、3。</p>
        </div>
        <div class="explain" v-show="item==='HMA'">
          <p>高价平均线</p>
          <p>一般移动平均线以收盘价为计算基础，高价平均线是以每日最高价为计算基础。目前市场上许多投资人将其运用在空头市场，认为它的压力效应比传统平均线更具参考价值。</p>
        </div>
        <div class="explain" v-show="item==='JLHB'">
          <p>绝路航标</p>
          <p>JLHB绝路航标指标是个技术指标，当出现紫色向上箭头时是买入信号的发出，综合了动量观念、强弱指标与移动平均线的优点，在计算过程中主要研究高低价位与收市价的关系，反映价格走势的强弱和超买超卖现象。</p>
        </div>
        <div class="explain" v-show="item==='RSI'">
          <p>相对强弱指标</p>
          <p>RSI最早被用于期货交易中，后来人们发现用该指标来指导股票市场投资效果也十分不错，并对该指标的特点不断进行归纳和总结。现在，RSI已经成为被投资者应用最广泛的技术指标之一。投资的一般原理认为，投资者的买卖行为是各种因素综合结果的反映，行情的变化最终取决于供求关系，而RSI指标正是根据供求平衡的原理，通过测量某一个期间内股价上涨总幅度占股价变化总幅度平均值的百分比，来评估多空力量的强弱程度，进而提示具体操作的。</p>
        </div>
        <div class="explain" v-show="item==='DDI'">
          <p>方向标准离差指数</p>
          <p>DDI指标一般用于观察一段时间内股价相对于前一天向上波动和向下波动的比例，并对其进行移动平均分析。通过分析DDI柱状线，可以判断是买入信号还是卖出信号</p>
        </div>
        <div class="explain" v-show="item==='CDP'">
          <p>逆势操作系统</p>
          <p>CDP,即“逆势操作系统”(就是在上升过程中做回挡,下跌过程中做反弹的系统),它是一种根据动量领域变化所定的股价界限。以前一天的行情波动来求出五个数值将当天及未来行情做一个划分。再以当日的开盘价决定如何操作。</p>
        </div>
