          break;
        case 'KDJ':
          this.$set(this.param,'Kyclec1',this.setParam[tck].Kyclec1);
          this.$set(this.param,'Kyclec2',this.setParam[tck].Kyclec2);
          this.$set(this.param,'Kyclec3',this.setParam[tck].Kyclec3);
          break;
        case 'DMA':
          this.$set(this.param,'Dparam1',this.setParam[tck].Dparam1);
          this.$set(this.param,'Dparam2',this.setParam[tck].Dparam2);
          this.$set(this.param,'Dparam3',this.setParam[tck].Dparam3);
          break;
        case 'RSI6':
          this.$set(this.param,'RSI6',this.setParam[tck].param);
          break;
        case 'RSI12':
          this.$set(this.param,'RSI12',this.setParam[tck].param);
          break;
        case 'RSI24':
          this.$set(this.param,'RSI24',this.setParam[tck].param);
          break;
        case 'HMA6':
          this.$set(this.param,'HMA6',this.setParam[tck].param)
          break;
        case 'HMA12':
          this.$set(this.param,'HMA12',this.setParam[tck].param)
          break;
        case 'HMA30':
          this.$set(this.param,'HMA30',this.setParam[tck].param)
          break;
        case 'DDI':
          this.$set(this.param,'DDI1',this.setParam[tck].param1);
          this.$set(this.param,'DDI2',this.setParam[tck].param2);
          this.$set(this.param,'DDI3',this.setParam[tck].param3);
          this.$set(this.param,'DDI4',this.setParam[tck].param4);
          break;
        case 'JLHB':
          this.$set(this.param,'Jparam1',this.setParam[tck].Jparam1);
          this.$set(this.param,'Jparam2',this.setParam[tck].Jparam2);
          break;
      }
    }
    this.ClientHeight=document.getElementsByTagName('body')[0].offsetHeight;
    let this_=this;
    window.addEventListener('resize', function () {
        let nowClientHeight = document.getElementsByTagName('body')[0].offsetHeight;
        if(nowClientHeight<this_.ClientHeight){
          this_.ClientHeight=nowClientHeight;
        }
        if(nowClientHeight>this_.ClientHeight){
          document.getElementsByClassName('setline')[0].style.transform='translateY(0px)';
          this_.ClientHeight=nowClientHeight;
        }
    });
  },
  mounted(){
    document.getElementsByClassName('setline')[0].style.height=document.getElementsByClassName('setline')[0].offsetHeight+'px';
    document.getElementsByClassName('setline')[0].style.fontSize=14/375*document.getElementsByClassName('setline')[0].offsetHeight+'px';
    document.getElementsByClassName('header')[0].style.fontSize=14/375*document.getElementsByClassName('setline')[0].offsetHeight+'px';
    document.getElementsByClassName('con-left')[0].style.fontSize=12/375*document.getElementsByClassName('setline')[0].offsetHeight+'px';
    document.getElementsByClassName('line1')[0].style.height=document.getElementsByClassName('line1')[0].offsetHeight+'px';
    document.getElementsByClassName('line2')[0].style.height=document.getElementsByClassName('line2')[0].offsetHeight+'px';
    document.getElementsByClassName('line3')[0].style.height=document.getElementsByClassName('line3')[0].offsetHeight+'px';
    document.getElementsByClassName('line4')[0].style.height=document.getElementsByClassName('line4')[0].offsetHeight+'px';
    this.lin1H= document.getElementsByClassName('line1')[0].offsetHeight;
  },
  methods:{
    changHeight(num){
      document.getElementsByClassName('setline')[0].style.transform='translateY(-'+this.lin1H*num+'px)';
    },
    backNomal(){
      document.getElementsByClassName('setline')[0].style.transform='translateY(0px)';
    },
    back(){
      let this_=this;
      this.$router.go(-1);
      localStorage.setP=JSON.stringify(this_.setParam);
      this.$emit('changeLine',this.setParam)
    },
    itemType(num){
      this.type=num;
    },
    selectType(e){
      this.item=e.target.innerText;
    },
    setDefault(){
      switch (this.item) {
        case 'MA':
          this.$set(this.param,'MA5',this.Params['MA5'].param)
          this.$set(this.param,'MA10',this.Params['MA10'].param)
          this.$set(this.param,'MA20',this.Params['MA20'].param)
          this.$set(this.param,'MA40',this.Params['MA40'].param)
          break;
        case 'BOLL':
          this.$set(this.param,'Bparam1',this.Params['BOLL'].Bparam1);
          this.$set(this.param,'Bparam2',this.Params['BOLL'].Bparam2);
          break;
        case 'EMA':
          this.$set(this.param,'EMA5',this.Params['EMA5'].param);
          this.$set(this.param,'EMA12',this.Params['EMA12'].param);
          this.$set(this.param,'EMA20',this.Params['EMA20'].param);
          break;
        case 'BBI':
          this.$set(this.param,'BBI1',this.Params['BBI'].param1);
          this.$set(this.param,'BBI2',this.Params['BBI'].param2);
          this.$set(this.param,'BBI3',this.Params['BBI'].param3);
          this.$set(this.param,'BBI4',this.Params['BBI'].param4);
          break;
        case 'MACD':
          this.$set(this.param,'MshortCycle',this.Params['MACD'].MshortCycle);
          this.$set(this.param,'MlongCycle',this.Params['MACD'].MlongCycle);
          this.$set(this.param,'Mparam',this.Params['MACD'].Mparam);
          break;
        case 'KDJ':
          this.$set(this.param,'Kyclec1',this.Params['KDJ'].Kyclec1);
          this.$set(this.param,'Kyclec2',this.Params['KDJ'].Kyclec2);
          this.$set(this.param,'Kyclec3',this.Params['KDJ'].Kyclec3);
          break;
        case 'DMA':
          this.$set(this.param,'Dparam1',this.Params['DMA'].Dparam1);
          this.$set(this.param,'Dparam2',this.Params['DMA'].Dparam2);
          this.$set(this.param,'Dparam3',this.Params['DMA'].Dparam3);
          break;
        case 'RSI':
          this.$set(this.param,'RSI6',this.Params['RSI6'].param);
          this.$set(this.param,'RSI12',this.Params['RSI12'].param);
          this.$set(this.param,'RSI24',this.Params['RSI24'].param);
          break;
        case 'HMA':
          this.$set(this.param,'HMA6',this.Params['HMA6'].param)
          this.$set(this.param,'HMA12',this.Params['HMA12'].param)
          this.$set(this.param,'HMA30',this.Params['HMA30'].param)
          break;
        case 'DDI':
          this.$set(this.param,'DDI1',this.Params['DDI'].param1);
          this.$set(this.param,'DDI2',this.Params['DDI'].param2);
          this.$set(this.param,'DDI3',this.Params['DDI'].param3);
          this.$set(this.param,'DDI4',this.Params['DDI'].param4);
          break;
        case 'JLHB':
          this.$set(this.param,'Jparam1',this.Params['JLHB'].Jparam1);
          this.$set(this.param,'Jparam2',this.Params['JLHB'].Jparam2);
          break;
      }
    }
  },
  watch:{
    param:{
      handler(newVal){
       this.$set(this.setParam.MA5,'param',newVal.MA5);
       this.$set(this.setParam.MA10,'param',newVal.MA10);
       this.$set(this.setParam.MA20,'param',newVal.MA20);
       this.$set(this.setParam.MA40,'param',newVal.MA40);
       this.$set(this.setParam.BOLL,'Bparam1',newVal.Bparam1);
       this.$set(this.setParam.BOLL,'Bparam2',newVal.Bparam2);
       this.$set(this.setParam.EMA5,'param',Number(newVal.EMA5));
       this.$set(this.setParam.EMA12,'param',Number(newVal.EMA12));
       this.$set(this.setParam.EMA20,'param',Number(newVal.EMA20));
       this.$set(this.setParam.BBI,'param1',Number(newVal.BBI1));
       this.$set(this.setParam.BBI,'param2',Number(newVal.BBI2));
       this.$set(this.setParam.BBI,'param3',Number(newVal.BBI3));
       this.$set(this.setParam.BBI,'param4',Number(newVal.BBI4));
       this.$set(this.setParam.MACD,'MshortCycle',Number(newVal.MshortCycle));
       this.$set(this.setParam.MACD,'MlongCycle',Number(newVal.MlongCycle));
       this.$set(this.setParam.MACD,'Mparam',Number(newVal.Mparam));
       this.$set(this.setParam.KDJ,'Kyclec1',Number(newVal.Kyclec1));
       this.$set(this.setParam.KDJ,'Kyclec2',Number(newVal.Kyclec2));
       this.$set(this.setParam.KDJ,'Kyclec3',Number(newVal.Kyclec3));
       this.$set(this.setParam.DMA,'Dparam1',Number(newVal.Dparam1));
       this.$set(this.setParam.DMA,'Dparam2',Number(newVal.Dparam2));
       this.$set(this.setParam.DMA,'Dparam3',Number(newVal.Dparam3));
       this.$set(this.setParam.RSI6,'param',Number(newVal.RSI6));
       this.$set(this.setParam.RSI12,'param',Number(newVal.RSI12));
       this.$set(this.setParam.RSI24,'param',Number(newVal.RSI24));
       this.$set(this.setParam.HMA6,'param',Number(newVal.HMA6));
       this.$set(this.setParam.HMA12,'param',Number(newVal.HMA12));
       this.$set(this.setParam.HMA30,'param',Number(newVal.HMA30));
       this.$set(this.setParam.DDI,'param1',Number(newVal.DDI1));
       this.$set(this.setParam.DDI,'param2',Number(newVal.DDI2));
       this.$set(this.setParam.DDI,'param3',Number(newVal.DDI3));
       this.$set(this.setParam.DDI,'param4',Number(newVal.DDI4));
       this.$set(this.setParam.JLHB,'Jparam1',Number(newVal.Jparam1));
       this.$set(this.setParam.JLHB,'Jparam2',Number(newVal.Jparam2));
      },
      deep:true
    }
  }
}
</script>

<style lang="scss" scoped>
  @import "../../style/theme2";
  .setline {
    width: 100vw;
    height: 100vh;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 9999;
    transition: all 0.3s;
    .header {
      width: 100vw;
      height: 13%;           //48px
      background-color: #ffffff;
      text-align: center;
      position: relative;
      z-index: 2;
      .head-con {
        width: tovhw(228);
        height: 70%;        //34px
        display: flex;
        background-color: #FFF;
        position: absolute;
        left: 50%;
        top: 18%;
        transform: translateX(-50%);
        border-radius: 4px;
        &>span:nth-child(1){
          border-right-width: 0;
          border-radius: 4px 0 0 4px;
        }
        &>span:nth-child(2){
          border-left-width: 0;
          border-radius:  0 4px  4px 0;
        }
        &>span {
          display: inline-block;
          flex: 1;
          /*line-height: tovh(32);*/
          padding-top: 2%;
          border: 1px solid  #999999;
          color: #999999;
          &.changc {
            border: 1px solid  #222222;
            color: #222222;
          }
        }

      }
      .back {
        display: inline-block;
        height: 100%;
        width: 40px;
        position: absolute;
        padding-top: 2%;
        left: tovhw(16);
        text-align: left;
        top: 0;
        font-family: 'iconfont';
      }
      .defaultset{
        display: inline-block;
        height: 100%;
        padding-top: 1%;
        position: absolute;
        right: tovhw(26);
        top: 0;
        font-family: 'iconfont';
      }
      .defaultset:active{
        background-color: rgba(25, 136, 255, 0.03);
      }
    }
    .content {
      width: 100vw;
      height: 87%;     //326px
      background-color: #fff;
      padding: 0 tovhw(50) 0 tovhw(20);
      position: relative;
      z-index: 2;
      .con-left {
        display: inline-block;
        flex-direction: column;
        height: 100%;
        width: tovhw(130);
        overflow-y: scroll;
        border-right: tovhw(1) solid #E5E5E5;
        .con-left-item {
          width: 100%;
          height: 9%;
          padding-top: 1%;
          color: #222222;
          &.changC {
            color: #1988FF;
          }
        }
      }
      .con-right{
        display: inline-block;
        width: tovhw(460);
        vertical-align: top;
        height: 100%;
        padding-top: 8%;
        padding-left: 11%;
        .line1,.line2,.line3,.line4{
          position: relative;
          height: tovh(33);
          margin-bottom: 2%;
          &>span:nth-child(1){
            display: inline-block;
            vertical-align: top;
            height: 100%;
            padding-top: 1.5%;
            margin-right: 3%;
            width: 25%;
          }
          input {
            width: tovhw(114);
            border-radius: 4px;
            height: 100%;
            outline-style: none ;
            border: 1px solid #222222 ;
            text-shadow: none ;
            text-align: center;
          }
        };
        .line1 {
          &>span:nth-child(1){
            color:#C51583;
          }
        }
        .line2 {
          &>span:nth-child(1){
            color:#F9C812;
          }
        }
        .line3 {
          &>span:nth-child(1){
            color:#3D90C9;
          }
        }
        .line4 {
          &>span:nth-child(1){
            color:#8572E7;
          }
        }
        .explain {
          height: tovh(200);
          padding-bottom: tovh(50);
          overflow-y: scroll;
          p:nth-child(1){
            font-weight: 600;
          }
        }
      }
    }
  }
</style>



